import React from 'react';
import { connect } from 'react-redux'
import DrinksCount from '../../../drinks/partials/DrinksCount_'
import { setDrinksPrefsView } from '../../../drinks/drinks-actions'
import { DRINKS_PREFS_VIEW_INGS, DRINKS_PREFS_VIEW_DRINKS_LIST } from '../../../drinks/drinks-reducer'
import './category-aside.scss'
import './category-aside-prefs.scss'

const stateToProps = state => {
    const drinks = state.drinks.drinks
    return {
        isShowingDrinksAllowed: drinks.length && state.drinks.origDrinks.length !== drinks.length,
        drinks,
        view: state.drinks.drinksPrefs.view
    };
};

function dispatchToProps(dispatch) {
    return {
        setDrinksPrefsIngsView: () => dispatch(
            setDrinksPrefsView(DRINKS_PREFS_VIEW_INGS)),

        setDrinksPrefsListView: () => dispatch(
            setDrinksPrefsView(DRINKS_PREFS_VIEW_DRINKS_LIST))
    };
}

function CategoryAsidePrefs({ view, drinks, setDrinksPrefsIngsView, setDrinksPrefsListView, isShowingDrinksAllowed }) {

    const showBtn = (<button
        className='cat-aside cat-aside-prefs'
        disabled={!isShowingDrinksAllowed}
        onClick={() => isShowingDrinksAllowed ? setDrinksPrefsListView() : false}>
        POKAŻ
    </button>)

    const backBtn = (<button
        className='cat-aside-prefs'
        onClick={() => setDrinksPrefsIngsView()}>
        WRÓĆ DO PREFERENCJI?
    </button>)

    const btn = view === DRINKS_PREFS_VIEW_INGS
        ? showBtn
        : backBtn

    return (<div className='cat-aside cat-aside-prefs'>

        <DrinksCount drinks={drinks} />

        { btn}

    </div>)
}

export default connect(stateToProps, dispatchToProps)(CategoryAsidePrefs)