import React from 'react';
import './category-aside.scss'
import CategoryIcon from '../../partials/CategoryIcon_'
import CategoryName from '../../partials/CategoryName_'
import HomeButton from '../../partials/CategoriesHomeButton_'

function CategoryAside_({ category, cssClassSuffix, children, showCategories }) {

    const cssClass = 'cat-aside-' + cssClassSuffix

    return (<aside className={cssClass}>
        <CategoryIcon category={category} />
        <CategoryName category={category} />

        {children}

        <nav className={cssClass}>
            <HomeButton action={showCategories} />
        </nav>
    </aside >)
}

export default CategoryAside_
