import usbProxy from './usb'
import { setAppState } from '../app/app-actions'
import { usbMsgFromDevice, usbInit, usbConnectGui, usbConnectDevice } from './usb-actions'
import {
    APP_STATE_USB_INITIALIZING,
    APP_STATE_USB_GUI_CONNECTING, APP_STATE_USB_GUI_CONNECTED,
    APP_STATE_USB_DEVICE_CONNECTING, APP_STATE_USB_DEVICE_CONNECTED,
    APP_STATE_USB_DEVICE_ERROR, APP_STATE_USB_DEVICE_DETACHED
} from '../app/app-reducer'
import tmkusbpluginMock from './tmkUsbPlugin-mock'
import json from '../tools/json'
import logger from '../tools/log'
const { fromjson } = { ...json }
const { err } = { ...logger }

const usbMiddleware = store => next => action => {
    if (usbInit.type === action.type) {
        store.dispatch(setAppState(APP_STATE_USB_INITIALIZING))

        try {
            usbProxy.addOnReadSucClbk('action', msg => store.dispatch(usbMsgFromDevice(msg)));
            usbProxy.addOnReadErrClbk('action', msg => store.dispatch(usbMsgFromDevice(msg)));

            store.dispatch(usbConnectGui(action.payload))
        } catch (e) {
            err(e)
        }
    }

    if (usbConnectGui.type === action.type) {
        store.dispatch(setAppState(APP_STATE_USB_GUI_CONNECTING))

        try {
            const params = action.payload.params || {}
            const cordova = action.payload.cordova;

            if (!!cordova) {
                usbProxy.connectGui(cordova.plugins.tmkusb, params)
            } else {
                usbProxy.connectGui(tmkusbpluginMock, params)
            }
        } catch (e) {
            err(e)
        }
    }

    if (usbConnectDevice.type === action.type) {
        store.dispatch(setAppState(APP_STATE_USB_DEVICE_CONNECTING))

        const params = action.payload || {}
        usbProxy.connectDevice(params)
    }

    if (usbMsgFromDevice.type === action.type) {

        const fromUsb = fromjson(action.payload || '{}')

        if (fromUsb.type === 'gui' && fromUsb.data === 'connected') {
            store.dispatch(setAppState(APP_STATE_USB_GUI_CONNECTED))
            store.dispatch(usbConnectDevice())
        }

        if (fromUsb.type === 'device.connecting' && fromUsb.data === 'connecting') {
            store.dispatch(setAppState(APP_STATE_USB_DEVICE_CONNECTING))
        }

        if (fromUsb.type === 'device.connected' && fromUsb.data === 'connected') {
            store.dispatch(setAppState(APP_STATE_USB_DEVICE_CONNECTED))
        }

        if (fromUsb.type === 'device.error' && fromUsb.data === 'error') {
            store.dispatch(setAppState(APP_STATE_USB_DEVICE_ERROR))
        }

        if (fromUsb.type === 'device.detached' && fromUsb.data === 'detached') {
            store.dispatch(setAppState(APP_STATE_USB_DEVICE_DETACHED))
        }

    }

    next(action);
}

export default usbMiddleware;