import { random } from 'lodash'

import { chooseCategory } from '../categories/categories-actions'
import { setRandomDrink, setTopDrinks } from '../drinks/drinks-actions'
import { showLoading, showApp } from './app-actions';

const appMiddleware = store => next => action => {

    if (action.type === chooseCategory.type) {

        if (action.payload.id === 'random') {
            store.dispatch(setRandomDrink())
        }

        if (action.payload.id === 'top') {
            store.dispatch(setTopDrinks())
        }
    }

    if (action.type === showLoading.type) {
        setTimeout(() => store.dispatch(showApp()),
            random(1000, 3000))
    }

    next(action);
}

export default appMiddleware;
