import React from 'react';
import { connect } from 'react-redux';
import DrinksList from './DrinksList'
import Keyboard from '../../tools/Keyboard'
import { DRINKS_LIST_VIEW_FILTER, DRINKS_LIST_VIEW_LIST } from '../drinks-reducer'
import './drinks-list-view.scss'

const stateToProps = state => {
    return {
        view: state.drinks.drinksList.view,
    };
};

function DrinksListView({ view }) {

    if (DRINKS_LIST_VIEW_LIST === view) {
        return <DrinksList />
    }

    if (DRINKS_LIST_VIEW_FILTER) {
        return (<>
            <DrinksList cssClass='drinks-list-compact' />
            <Keyboard />
        </>)
    }

    return 'NO PROPER VIEW'
}

export default connect(stateToProps)(DrinksListView);
