import React from 'react';
import './category-icon.scss'

function CategoryIcon_({ category }) {

    const cat = 'cat-' + category.id;

    return (<div className={'category-icon ' + cat}>

        <svg className={'category-icon ' + cat}
            viewBox="0 0 100 100">
            {/* preserveAspectRatio="meet" > */}

            <circle className={'category-icon ' + cat}
                cx="50" cy="50" r="50" />

            <image href={'categories/' + cat + '.svg'}
                className={'category-icon ' + cat} />

        </svg>

    </div>)
}

export default CategoryIcon_
