import React from 'react';
import './drink.scss'
import { connect, batch } from 'react-redux';
import { showLoading } from '../../app/app-actions'
import { resetDrinks } from '../drinks-actions'
import { showCategories } from '../../categories/categories-actions'
import DrinkName from '../partials/DrinkName_'
import DrinkIngs from '../partials/DrinkIngs_'
import DrinkIcon from '../partials/DrinkIcon_'

const stateToProps = state => {
    return { drink: state.drinks.drink };
};

function dispatchToProps(dispatch) {
    return {
        chooseDrink: d => {
            // https://redux.js.org/faq/performance/#how-can-i-reduce-the-number-of-store-update-events
            batch(() => {
                dispatch(resetDrinks())
                dispatch(showCategories())
                dispatch(showLoading())
            })
        }
    };
}

function Drink({ drink, chooseDrink }) {
    return (<div className='drink'>

        <DrinkName drink={drink} />
        <DrinkIcon drink={drink} />
        <DrinkIngs drink={drink} />

        <nav className='drink'>
            <button className='drink'
                disabled={!drink.ings}
                onClick={() => chooseDrink(drink)}>
                ZRÓB DRINKA
                </button>
        </nav>
    </div >)
}

export default connect(stateToProps, dispatchToProps)(Drink);