import React from 'react'
import { connect } from 'react-redux'
import { chooseCategory } from './categories-actions'
import CategoryIcon from './partials/CategoryIcon_'
import CategoryName from './partials/CategoryName_'

const stateToProps = state => {
    return {
        categories: state.categories.categories,
        drinksPresent: state.drinks.drinks.length
    };
};

function dispatchToProps(dispatch) {
    return {
        chooseCategory: cat => dispatch(chooseCategory(cat))
    };
}

function Categories({ categories, drinksPresent, chooseCategory }) {

    const choices = categories.map(
        c => (<li key={c.id}
            className='categories'
            onClick={() => drinksPresent ? chooseCategory(c) : null}>

            <CategoryIcon category={c} />

            <CategoryName category={c} />

        </li>)
    )

    const cssClass = drinksPresent ? '' : 'categories-inactive'

    return <ul className={'categories ' + cssClass}>
        {choices}
    </ul>
}

export default connect(stateToProps, dispatchToProps)(Categories);
