import { filter, map } from 'lodash'

function extractIngs(drinks = []) {

    return drinks.reduce((acc, d) => {

        d.ings.forEach((ing, idx) => {
            acc[ing] = {
                id: ing,
                name: ing,
                usable: true,
                wanted: false,
                notWanted: false
            }
        })

        return acc
    }, {})
}

function setUsableIng(ings = {}, ing = '', flag = true) {
    const io = {}
    io[ing] = { ...ings[ing], ...{ usable: flag } }
    return { ...ings, ...io }
}

function getUsableIngIds(ings = {}, flag = true) {
    return map(
        filter(ings, ['usable', flag]),
        'id'
    )
}

function getWantedIngIds(ings = {}) {
    return map(
        filter(ings, ['wanted', true]),
        'id'
    )
}

function getNotWantedIngIds(ings = {}) {
    return map(
        filter(ings, ['notWanted', true]),
        'id'
    )
}


export {
    extractIngs, setUsableIng, getUsableIngIds, getWantedIngIds, getNotWantedIngIds
}