import _ from 'lodash'

let usbMsgSuc = () => { }
let usbMsgErr = () => { }

export default {

    connectGui: function (params, successCallback, errorCallback) {
        setTimeout(function () {
            successCallback('{"type": "gui", "data":"connected"}')
            usbMsgSuc = successCallback
            usbMsgErr = errorCallback
        }, _.random(1000, 3000))
    },

    connectDevice: function (params, successCallback, errorCallback) {
        setTimeout(function () {
            usbMsgSuc('{"type": "device.connected", "data":"connected"}')
        }, _.random(3000, 5000))

        // setTimeout(function () {
        //     usbMsgSuc('setData')
        // }, _.random(3000, 5000))
    },

    write: function (text, successCallback, errorCallback) {
        errorCallback(text)

    },

    dispatch: function (command, successCallback, errorCallback) {
        errorCallback(command)
    }
}
