import React from 'react';
import { connect } from 'react-redux'
import { DRINKS_LIST_VIEW_FILTER, DRINKS_LIST_VIEW_LIST } from '../drinks-reducer'
import { setDrinksListView } from '../drinks-actions'
import { filterDrinks } from '../../drinks/drinks-actions'

const stateToProps = state => {
    const { view, nameOrIng } = state.drinks.drinksList
    return {
        view,
        nameOrIng
    };
};

function dispatchToProps(dispatch) {

    return {
        setDrinksListView: (view => {
            if (DRINKS_LIST_VIEW_LIST === view) {
                dispatch(filterDrinks(''))
            }

            dispatch(setDrinksListView(view))
        }),
        filterDrinks: nameOrIng => dispatch(filterDrinks(nameOrIng))
    };
}

function DrinksFilter({ view, nameOrIng, setDrinksListView, filterDrinks }) {

    let toggleFilterBtn = ''
    if (DRINKS_LIST_VIEW_LIST === view) {
        toggleFilterBtn = <button
            onClick={() => setDrinksListView(DRINKS_LIST_VIEW_FILTER)}
            className='drinks-filter'>Wyszukaj po nazwie lub składniku</button>
    }

    if (DRINKS_LIST_VIEW_FILTER === view) {
        toggleFilterBtn = <button
            onClick={() => setDrinksListView(DRINKS_LIST_VIEW_LIST)}
            className='drinks-filter'>Wyłącz filtrowanie</button>
    }

    let clearBtn = DRINKS_LIST_VIEW_FILTER === view
        ? <button onClick={() => filterDrinks('')}
            disabled={!nameOrIng}
            className='drinks-filter'>Wyczyść</button>
        : ''

    return (<>
        {toggleFilterBtn}

        {clearBtn}

    </>)
}

export default connect(stateToProps, dispatchToProps)(DrinksFilter)