import React from 'react';
import './drink-ings.scss'

function DrinkIngs_({ drink }) {
    return (<ul className='drink-ings'>
        {(drink.ings || [])
            .map((val, idx, arr) => {

                const sep = (<li key={drink.id + '-ings-sep' + idx}
                    className='drink-ings drink-ings-sep'>&nbsp;</li>)

                return (<React.Fragment key={drink.id + '-ings-fragment' + idx}>
                    <li key={drink.id + '-ings' + idx}
                        className='drink-ings'>
                        {val}
                    </li>

                    {sep}

                </React.Fragment>)
            })
        }
    </ul>)
}

export default DrinkIngs_
