import React from 'react';
import { connect } from 'react-redux'
import './drink-glass.scss'

const stateToProps = state => {
    const { max, total, ingsOrder } = { ...state.drinks.drinkCustom }
    return {
        ings: state.drinks.ings,
        max,
        total,
        ingsOrder
    };
};

function DrinksGlass({ ings, max, total, ingsOrder }) {

    const bottomPadding = 10
    const heighSpace = 85 - bottomPadding
    const level = total / max
    const levelCorrection = level ? 1.5 - (Math.sqrt(level) / 2) : 1
    const levelHeight = (bottomPadding + level * heighSpace) * levelCorrection
    const ingsOrderReversed = ingsOrder.map(io => io)
    ingsOrderReversed.reverse()

    return (<div className='drink-custom-glass'>

        <div style={{ height: levelHeight ? levelHeight + '%' : levelHeight }}
            className='drink-custom-glass-level'>
        </div>

        <svg className='drink-custom-glass drink-custom-glass1'
            viewBox='0 0 240 240'>
            <use xlinkHref='#drink-custom-glass1'></use>
        </svg>

        <svg className='drink-custom-glass drink-custom-glass2'
            viewBox='0 0 240 240'>
            <use xlinkHref='#drink-custom-glass2'></use>
        </svg>

        <svg className='drink-custom-glass drink-custom-glass3'
            viewBox='0 0 240 240'>
            <use xlinkHref='#drink-custom-glass3'></use>
        </svg>

        <svg className='drink-custom-glass drink-custom-glass-bg'
            viewBox='0 0 240 240'>
            <use xlinkHref='#drink-custom-glass-bg'></use>
        </svg>

        <ul style={{ height: levelHeight ? levelHeight + '%' : levelHeight }}
            className='drink-custom-glass-level'>
            {ingsOrderReversed.map(ingId => {

                return (<li key={'d-c-g-' + ingId}
                    className='drink-custom-glass-level'>
                    {ings[ingId].name}
                </li>)
            })}
        </ul>
    </div>)
}

export default connect(stateToProps)(DrinksGlass)