import React from 'react'
import './category.scss'
import CategoryAside from './aside/CategoryAside_'
import CategoryAsideList from './aside/CategoryAsideList'
import CategoryAsidePrefs from './aside/CategoryAsidePrefs'
import CategoryAsideCustom from './aside/CategoryAsideCustom'

import DrinksAlco from '../../drinks/drinks-alco/DrinksAlco'
import DrinksCustom from '../../drinks/drinks-custom/DrinksCustom'
import DrinksList from '../../drinks/drinks-list/DrinksListView'
import DrinksPrefs from '../../drinks/drinks-prefs/DrinksPrefs'
import DrinksRandom from '../../drinks/drinks-random/DrinksRandom'
import DrinksTop from '../../drinks/drinks-top/DrinksTop'

import { connect, batch } from 'react-redux'
import { showCategories } from '../categories-actions'
import { resetDrinks } from '../../drinks/drinks-actions'

const drinksViews = {
    DrinksAlco,
    DrinksCustom,
    DrinksList,
    DrinksPrefs,
    DrinksRandom,
    DrinksTop
}

const drinksAsides = {
    CategoryAsideList,
    CategoryAsidePrefs,
    CategoryAsideCustom
}

const stateToProps = state => {
    const cat = state.categories.category
    const catSuffix = cat.id[0].toUpperCase() + cat.id.slice(1)
    const catAsideViewName = 'CategoryAside' + catSuffix
    const drinksViewName = 'Drinks' + catSuffix

    return {
        cat,
        DrinksView: drinksViews[drinksViewName],
        AsideContent: drinksAsides[catAsideViewName] || ''
    };
};

function dispatchToProps(dispatch) {
    return {
        showCategories: () => {
            batch(() => {
                dispatch(resetDrinks())
                dispatch(showCategories())
            })
        }
    };
}

function Category({ cat, showCategories, DrinksView, AsideContent }) {

    const catCssClass = 'category-' + cat.id + ' category-aside-' + cat.aside

    const asideContent = AsideContent
        ? <AsideContent category={cat} />
        : <div className={'cat-aside-' + cat.aside + '-spacer'}>&nbsp;</div>

    return (<div className={'category ' + catCssClass}>

        <DrinksView />

        <CategoryAside category={cat}
            cssClassSuffix={cat.aside}
            showCategories={showCategories} >

            {asideContent}

        </CategoryAside>

    </div>)
}

export default connect(stateToProps, dispatchToProps)(Category);
