import React from 'react';
import { connect } from 'react-redux'
import { filterDrinks } from '../drinks/drinks-actions'
import './keyboard.scss'

const stateToProps = state => {
    const { drinksList } = state.drinks
    return {
        nameOrIng: drinksList.nameOrIng
    };
};

function dispatchToProps(dispatch) {
    return {
        filterDrinks: nameOrIng => dispatch(filterDrinks(nameOrIng))
    };
}

const r0 = ['ą', 'ć', 'ę', 'ł', 'ń', 'ó', 'ś', 'ź', 'ż']
const r1 = ['q', 'w', 'e', 'r', 't', 'y', 'u', 'i', 'o', 'p']
const r2 = ['a', 's', 'd', 'f', 'g', 'h', 'j', 'k', 'l']
const r3 = ['z', 'x', 'c', 'v', 'b', 'n', 'm']

function Keyboard({ nameOrIng, filterDrinks }) {

    const text = nameOrIng.length ? nameOrIng : 'min 3 litery'
    const textClass = nameOrIng.length ? ' ' : ' keyboard-txt-placeholder'

    return (<div className='keyboard'>

        <div className='keyboard-row keyboard-row-txt'>
            <span
                onClick={() => filterDrinks('')}
                key={'kbrdcls'}
                className='keyboard-btn'>
                czyść
                </span>
            <span className={'keyboard-txt' + textClass}>{text}</span>
            <span
                onClick={() => filterDrinks(nameOrIng.slice(0, -1))}
                className='keyboard-btn'>{'⌫'}</span>
        </div>

        <div className='keyboard-row keyboard-r0'>
            {r0.map(s => {
                return <span
                    onClick={() => filterDrinks(nameOrIng + s)}
                    key={'kbrd' + s}
                    className='keyboard-btn'>
                    {s}
                </span>
            })}
        </div>

        <div className='keyboard-row keyboard-r1'>
            {r1.map(s => {
                return <span
                    onClick={() => filterDrinks(nameOrIng + s)}
                    key={'kbrd' + s}
                    className='keyboard-btn'>
                    {s}
                </span>
            })}
        </div>

        <div className='keyboard-row keyboard-r2'>
            {r2.map(s => {
                return <span
                    onClick={() => filterDrinks(nameOrIng + s)}
                    key={'kbrd' + s}
                    className='keyboard-btn'>
                    {s}
                </span>
            })}
        </div>

        <div className='keyboard-row keyboard-r3'>
            {r3.map(s => {
                return <span
                    onClick={() => filterDrinks(nameOrIng + s)}
                    key={'kbrd' + s}
                    className='keyboard-btn'>
                    {s}
                </span>
            })}
        </div>

        <div className='keyboard-row keyboard-r4'>
            <span
                onClick={() => filterDrinks(nameOrIng + ' ')}
                key={'kbrd_0_'}
                className='keyboard-btn'>
                &nbsp;
                </span>
        </div>
    </div>)
}

export default connect(stateToProps, dispatchToProps)(Keyboard)
