import { intersection, random } from 'lodash'
import { getUsableIngIds, getWantedIngIds, getNotWantedIngIds } from '../ings/ings'

function disableDrinkByIngs(drink = [], ings = {}) {
    const notUsableIngIds = getUsableIngIds(ings, false)

    return {
        ...drink,
        ...{
            usable: !intersection(drink.ings, notUsableIngIds).length
        }
    }
}

function randDrink(drinks = []) {
    const idx = random(drinks.length - 1);
    return drinks[idx]
}

function determineUsableDrink(drink = {},
    usableIngs = [], wantedIngs = [], notWantedIngs = []) {

    const isUsable = !!intersection(drink.ings, usableIngs).length
    const isWanted = !wantedIngs.length || !!intersection(drink.ings, wantedIngs).length
    const isNotNotWanted = !intersection(drink.ings, notWantedIngs).length

    return {
        ...drink,
        ...{
            usable: isUsable && isWanted && isNotNotWanted
        }
    }
}

function selectDrinks(drinks = [], ings = {}) {

    const usableIngs = getUsableIngIds(ings)

    const wantedIngs = getWantedIngIds(ings)

    const notWantedIngs = getNotWantedIngIds(ings)

    return drinks
        .map(d => determineUsableDrink(d, usableIngs, wantedIngs, notWantedIngs))
        .filter(d => d.usable)
}

function makeNameAndIngsStr(drink = {}) {
    return {
        ...drink,
        nameAndIngsStr: drink.name.toLowerCase() + ' ' + drink.ings.join(' ').toLowerCase()
    }
}

function filterDrink(drink = [], nameOrIng = '') {
    const words = nameOrIng.toLowerCase().match(/\S+/g) || []
    console.log(" words =  ", words)

    return !words.length
        || words
            .filter(word => drink.nameAndIngsStr.indexOf(word) > -1)
            .length === words.length
}

export {
    randDrink, disableDrinkByIngs, selectDrinks, filterDrink, makeNameAndIngsStr
}