import React from 'react';
import DrinkList from '../drinks-list/DrinksList'
import Ings from '../../ings/Ings'
import { connect } from 'react-redux'
import './drinks-prefs.scss'

const drinksPrefsViews = {
    Ings,
    DrinkList
}

const stateToProps = state => {
    const drinksPrefsViewName = state.drinks.drinksPrefs.view

    return {
        DrinksPrefsView: drinksPrefsViews[drinksPrefsViewName]
    };
};

function DrinksPrefs({ props, DrinksPrefsView }) {
    return <DrinksPrefsView {...props} />
}

export default connect(stateToProps)(DrinksPrefs);
