import { createReducer } from '@reduxjs/toolkit'

import {
    showApp,
    showLoading,
    //
    setAppDisplay,
    setAppMode,
    setAppState,
    setAppView
} from './app-actions'

// DISPLAY
export const APP_DISPLAY_DRINKER = 'APP_DISPLAY_DRINKER'
export const APP_DISPLAY_AUX = 'APP_DISPLAY_AUX'

// VIEW
export const APP_VIEW = 'APP_VIEW'
export const APP_VIEW_LOADING = 'APP_VIEW_LOADING'

// STATE
export const APP_STATE_STARTED = 'APP_STATE_STARTED'
export const APP_STATE_USB_INITIALIZING = 'APP_STATE_USB_INITIALIZING'
export const APP_STATE_USB_GUI_CONNECTING = 'APP_STATE_USB_GUI_CONNECTING'
export const APP_STATE_USB_GUI_CONNECTED = 'APP_STATE_USB_GUI_CONNECTED'
export const APP_STATE_USB_DEVICE_CONNECTING = 'APP_STATE_USB_DEVICE_CONNECTING'
export const APP_STATE_USB_DEVICE_CONNECTED = 'APP_STATE_USB_DEVICE_CONNECTED'
export const APP_STATE_USB_DEVICE_ERROR = 'APP_STATE_USB_DEVICE_ERROR'
export const APP_STATE_USB_DEVICE_DETACHED = 'APP_STATE_USB_DEVICE_DETACHED'
export const APP_STATE_INITIALIZED = 'APP_STATE_INITIALIZED'

// MODE
export const APP_MODE_NORMAL = 'APP_MODE_NORMAL'
export const APP_MODE_DASHBOARD = 'APP_MODE_DASHBOARD'
export const APP_MODE_DASHBOARD_FULL_SCREEN = 'APP_MODE_DASHBOARD_FULL_SCREEN'

const initialState = {
    display: APP_DISPLAY_DRINKER,
    // display: APP_DISPLAY_AUX,
    //
    // view: APP_VIEW_LOADING,
    view: APP_VIEW,
    //
    state: APP_STATE_STARTED,
    //
    // mode: APP_MODE_NORMAL
    mode: APP_MODE_DASHBOARD
    // mode: APP_MODE_DASHBOARD_FULL_SCREEN
}

// https://redux-toolkit.js.org/api/createreducer/
const appReducer = createReducer(initialState, {
    [showApp]: (state, action) => {
        state.view = APP_VIEW
    },

    [showLoading]: (state, action) => {
        state.view = APP_VIEW_LOADING
    },

    //

    [setAppDisplay]: (state, action) => {
        state.display = action.payload
    },

    [setAppMode]: (state, action) => {
        state.mode = action.payload
    },

    [setAppState]: (state, action) => {
        state.state = action.payload
    },

    [setAppView]: (state, action) => {
        state.view = action.payload
    }
})

export default appReducer;