import React, { useEffect } from 'react'
import { connect } from "react-redux";
import './app/_app-variables.scss'
import './app/_app.scss'
import './App.scss'
import {
  APP_DISPLAY_DRINKER, // APP_DISPLAY_AUX,
  // APP_STATE_INITIALIZED,
  APP_VIEW, // APP_LOADING_VIEW,
  APP_MODE_NORMAL, APP_MODE_DASHBOARD, APP_MODE_DASHBOARD_FULL_SCREEN
} from './app/app-reducer'
import AppLoading from './app/AppLoading'
import Categories from './categories/CategoriesView'
import DrinkerAux from './drinker-aux/DrinkerAux'
import Dashboard from './dashboard/Dashboard'
import { usbInit } from './usb/usb-actions'

const stateToProps =
  state => {
    const AppDisplay = state.app.display === APP_DISPLAY_DRINKER
      ? Categories
      : DrinkerAux

    return ({
      AppView: state.app.view === APP_VIEW ? AppDisplay : AppLoading,
      appMode: state.app.mode,
      appState: state.app.state,
    })
  }

const dispatchToProps =
  dispatch => ({
    usbInit: payload => dispatch(usbInit(payload))
  })

/**
 * cordova set in index.html because here is not present in window
 * 
 */
function App({ cordova, appState, AppView, appMode, usbInit }) {

  useEffect(
    () => {
      usbInit({ cordova })
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []);

  if (appMode === APP_MODE_NORMAL) {
    return <AppView />
  }

  if (appMode === APP_MODE_DASHBOARD) {

    // if (appState !== APP_STATE_INITIALIZED) {
      return (<div id="app">
        <div id="app-view-wrapper">
          {appState}
        </div>
        <div id="app-dashboard-wrapper"><Dashboard /></div>
      </div>)
    // }

    // return (<div id="app">
    //   <div id="app-view-wrapper"><AppView /></div>
    //   <div id="app-dashboard-wrapper"><Dashboard /></div>
    // </div>)
  }

  if (appMode === APP_MODE_DASHBOARD_FULL_SCREEN) {
    return (<Dashboard />)
  }

  return <>
    <div>no proper mode</div>
    <div>{appState}</div>
    <div>{appMode}</div>
  </>
}

export default connect(stateToProps, dispatchToProps)(App);
