const nr = random(17, 17)

export const drinks = times(nr, i => ({
    id: 'drink-' + i,
    name: 'drink ' + i,
    desc: getLorems()['' + (i === 0 ? 0 : i % 9)],
    ings: getings()['' + (i === 0 ? 0 : i % 9)],
    icon: 'icon.png',
    usable: true,
    nameAndIngsStr: ''
}))

