import React from 'react';
import { connect } from 'react-redux'
import { ingCustomReset } from '../../../ings/ings-actions'
import DrinkGlass from '../../../drinks/partials/DrinkGlass'
import './category-aside.scss'
import './category-aside-custom.scss'

const stateToProps = state => {
    const { ingsOrder } = { ...state.drinks.drinkCustom }
    
    return {
        ingsOrder
    };
};

function dispatchToProps(dispatch) {
    return {
        ingsReset: () => dispatch(ingCustomReset())
    };
}

function CategoryAsideCustom({ ingsReset }) {

    return (<div className='cat-aside cat-aside-custom'>

        <DrinkGlass />

        <button onClick={() => ingsReset()} className='drink-custom-glass'>Resetuj</button>
        <button className='drink-custom-glass'>Zrób drinka</button>

    </div>)
}

export default connect(stateToProps, dispatchToProps)(CategoryAsideCustom)