import React from 'react';
import { connect } from 'react-redux';
import { setDrink } from '../drinks-actions'
import DrinksListItem from './DrinksListItem_'
import Drink from '../drink/Drink'
import './drinks-list.scss'

const stateToProps = state => {
    const { drink, drinks } = state.drinks

    return {
        drink,
        drinks
    };
};

function dispatchToProps(dispatch) {
    return { setDrink: drink => dispatch(setDrink(drink)) };
}

function DrinksList({ cssClass = '', drink, drinks, setDrink }) {

    console.log('cssClass = ', cssClass)

    if (!drinks.length) {
        return (<ul className={'drinks-list ' + cssClass}>
            <li className='drinks-list-empty'>
                brak drinków do wyświetlenia
            </li>
        </ul>)
    }

    return (<ul className={'drinks-list ' + cssClass}>
        {drinks
            .filter(d => d.usable)
            .map((d, idx) => {
                const isSelected = d.id === drink.id

                const cssClass = 'drinks-list '
                    + (isSelected ? 'drinks-list-selected' : '')

                const drinkToShow = isSelected ? { id: -1 } : d

                const ref = React.createRef();

                const handleClick = () => {
                    ref.current.scrollIntoView({
                        behavior: 'smooth',
                        block: 'center',
                    });

                    setDrink(drinkToShow)
                }

                return (<li key={d.id}
                    ref={ref}
                    className={cssClass}
                    onClick={handleClick}>

                    <span className='drinks-list-pos'>{idx + 1}</span>

                    {isSelected
                        ? (<Drink key={d.id + 'd'} />)
                        : (<DrinksListItem key={d.id + 'dli'} drink={d} />)}
                </li>)
            })}
    </ul >)
}

export default connect(stateToProps, dispatchToProps)(DrinksList);
