import { isFunction as isFun } from 'lodash'

function create() {

  let usb;
  const sucClbks = {}
  const errClbks = {}

  const sucClbk = msg => {
    Object.values(sucClbks)
      .forEach(c => c(msg))
  }

  const errClbk = msg => {
    Object.values(errClbks)
      .forEach(c => c(msg))
  }

  return {
    addOnReadSucClbk: (name = '', fn) => {
      if (!isFun(fn)) {
        throw new Error('Not a function: ' + name)
      }

      sucClbks[name] = fn
    },

    addOnReadErrClbk: (name = '', fn) => {
      if (!isFun(fn)) {
        throw new Error('Not a function: ' + name)
      }

      errClbks[name] = fn
    },

    connectGui: (usbPlugin, params) => {
      usb = usbPlugin
      usb.connectGui(params, sucClbk, errClbk)
    },

    connectDevice: params => {
      usb.connectDevice(params, sucClbk, errClbk)
    },

    exec: (name = '', msg = '') => {
      return new Promise((resolve, reject) => {
        msg.length
          ? usb[name](msg, resolve, reject)
          : usb[name](resolve, reject)
      })
    }

  }
}

const instance = create()

export {
  instance as default,
  create
}