import React from 'react';
import { sortBy, toArray } from 'lodash'
import { connect } from 'react-redux';
import { setDrink } from '../drinks/drinks-actions'
import IngsCustom_ from './ings-custom/IngCustom_'
import IngsPrefs_ from './ings-prefs/IngPrefs'
import './ings.scss'

const ingsViews = {
    IngsCustom_,
    IngsPrefs_
}

const stateToProps = state => {
    const cat = state.categories.category
    const ingsViewName = 'Ings' + cat.id[0].toUpperCase() + cat.id.slice(1) + '_';

    return {
        ings: sortBy(toArray(state.drinks.ings), 'name'),
        IngsView: ingsViews[ingsViewName]
    };
};

function dispatchToProps(dispatch) {
    return { setDrink: drink => dispatch(setDrink(drink)) };
}

function Ings({ ings, IngsView }) {
    return (<ul className='ings '>
        {ings
            .filter(ing => ing.usable)
            .map((ing, idx, arr) => {
                return (<li key={ing.id} className='ings '>
                    <IngsView key={ing.id} ing={ing}></IngsView>
                </li>)
            })
        }
    </ul>)
}

export default connect(stateToProps, dispatchToProps)(Ings);
